html, body {
  max-width: 100%;
  overflow-x: hidden;
}

  .Accueil {
    display: flex;
    padding-top:80px;
    padding-left: 20px;
    background: white;
    height: 830px;
    position:relative;   

}

.Accueil .Container{
  position:absolute;
  left:40%;
  top: 40%;
}

.Accueil .Container .Hello {
    font-size: 69px;
    font-family:'Binchotan_Sharp';
    position:relative;
    left:-35%;
    bottom:120px;
    font-family: 'Henny Penny', cursive;
    background-image:linear-gradient(90deg, rgba(158,188,235,1) 39%, rgba(124,159,220,1) 53%, rgba(53,74,112,1) 73%);
    background-color: white;
    background-clip: white;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; 
  }

.Accueil .Container .Jesuis {
  font-size: 40px;
  position:relative;
  left: -46.2%;
}


.Accueil .Programmer {
    position:absolute;
    height:50%;
    left:0;
    top:30%;  
}

  .Scroller {
    height: 3em;
    line-height: 3.2em;
    position: relative;
    overflow: hidden;
    width: 78em;
    left:-39%;
    bottom:70px;
    
  }
  .Scroller > span {
    text-align: left;
    position: absolute;
    top: 0;
    animation: slide 7s infinite;
    font-weight: bold;
    font-size: 45px;
    font-family: 'Lavishly Yours', cursive;
    color:#6495ED;
    
  }
  @keyframes slide {
    0% {
      top: 0;
    }
    35% {
      top: -1.1em;
    }
    90% {
      top: -2.4em;
    }
  }